}

@font-face {
  src: url("assassin.1648ee8e.woff2") format("woff2");
  font-family: assassin-st;
  font-weight: 700;
  font-style: italic;
}

::placeholder {
  color: #666;
}

::selection {
  background-color: var(--accent);
  color: #fff;
}

input, textarea {
  outline: none;
}

input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}

input:required:valid, textarea:required:valid {
  border-color: green;
}

body, html {
  height: 100%;
}

.showcase {
  height: 100%;
  color: #fff;
  background-color: #000;
  position: relative;
}

.showcase:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  box-shadow: inset 0 0 500px #000;
}

.showcase:after {
  content: "";
  background-image: url("light.08f1b2b1.png");
  background-position: 50% -65px;
  background-repeat: no-repeat;
  background-size: cover;
  animation: 3s ease-in-out infinite k-light;
  position: absolute;
  inset: 0;
}

.showcase__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: .5;
  position: absolute;
  top: 0;
  left: 0;
}

.showcase__content-wrapper {
  z-index: 2;
  height: 100%;
  padding: var(--padding) 0;
  position: relative;
}

.showcase__content-wrapper:before {
  content: "";
  background-image: url("glow.0640825a.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: 1.8s ease-in-out infinite k-glow;
  position: absolute;
  inset: 0;
}

.showcase__header {
  text-align: center;
  width: 100%;
  z-index: 1;
  color: #ffffffbf;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  position: absolute;
  top: 40px;
}

.showcase__header span {
  color: var(--accent);
}

.showcase-carousel__item {
  height: calc(100vh - var(--padding) * 2);
  text-align: center;
  opacity: .25;
  transition: opacity 1.8s var(--transition), transform 1.8s var(--transition);
  position: relative;
  transform: scale(.75);
}

.showcase-carousel__item.swiper-slide-active {
  opacity: .8;
  transform: scale(.98);
}

.showcase-carousel__image-wrapper > * {
  width: 100%;
  height: 100%;
  perspective: 150px;
  transform-style: preserve-3d;
  position: absolute;
}

.showcase-carousel__image {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}

@keyframes k-light {
  0% {
    opacity: .2;
  }

  50% {
    opacity: .3;
  }

  100% {
    opacity: .2;
  }
}

@keyframes k-glow {
  0% {
    opacity: .6;
  }

  50% {
    opacity: .8;
  }

  100% {
    opacity: .6;
  }
}

